import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import { Checkbox  } from "carbon-components-react";
import { EventAPI } from "../../../commons/EventAPI";

const dispatchedEvents = [EventAPI.CHECKBOX_CHANGED];

/*
    Example: <srs-checkbox label="Label" checked="true"/>
*/
const SrsCheckbox = function (props, ref) {
    const [isChecked, setIsChecked] = useState(props.checked || props.value || false);

    useEffect(() => {
        setIsChecked(props.checked || props.value || false);
        props.setValue(props.checked || props.value || false)
    }, [props]);
    return props.elementid? (
      <Checkbox 
        labelText={props.label}
        checked={isChecked}
        id={props.elementid}
        onChange={(evt, payload) => {
            setIsChecked(payload.checked);
            props.setValue(payload.checked);
            if(props.onChange) {
                props.onChange(payload.checked);
            }else {
                props.dispatchEvent(EventAPI.CHECKBOX_CHANGED, {payload: { value: payload.checked, id: props.elementid }});
            }
        }}
      />
    ): null;
}

const styles = `
    
`
const props = [{
    name: "label",
    defaultValue: "label",
    type: "string"
}, {
    name: "checked",
    defaultValue: false,
    type: "boolean"
}, {
    name: "value",
    defaultValue: false,
    type: "boolean"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsCheckbox), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots};